html {
  font-size: 38%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  line-height: 1.8;
  background-color: #dfdddd;
  color: #181818;
}

p {
  font-size: 3rem;
}

.contenedor {
  max-width: 90rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-size: 2.5rem;
  transition: 300ms all;
  color: #181818;
}

img, picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 3rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4.5rem;
}

h3 {
  font-size: 4rem;
}

h4 {
  font-size: 3.5rem;
}

@media (min-width: 768px) {
  .contenedor {
    max-width: 120rem;
    margin: 0 auto;
  }
}
@media (min-width: 1024px) {
  .contenedor {
    max-width: 160rem;
    margin: 0 auto;
  }
}
.seccion {
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.no-margin-top {
  margin-top: 0;
}

.padding {
  padding: 2rem;
}

.margin-top {
  margin-top: 5rem;
}

.boton-amarillo {
  color: #dfdddd;
  background-color: #E08709;
  padding: 1rem 4rem;
  border-radius: 1rem;
  text-transform: uppercase;
  margin: 2rem 0;
  font-weight: 900;
  border: 1px solid transparent;
  transition: 300ms all;
}
.boton-amarillo:hover {
  background-color: transparent;
  color: #181818;
  border: 1px solid #E08709;
}

.boton-verde {
  display: block;
  text-align: center;
  width: 100%;
  color: #dfdddd;
  background-color: #1a850b;
  padding: 2rem;
  border-radius: 1rem;
  text-transform: uppercase;
  font-weight: 900;
  border: 1px solid transparent;
  transition: 300ms all;
}
.boton-verde:hover {
  cursor: pointer;
  background-color: transparent;
  color: #181818;
  border: 1px solid #1a850b;
}

.boton-rojo {
  display: block;
  text-align: center;
  width: 100%;
  color: #dfdddd;
  background-color: #ad0f0f;
  padding: 2rem;
  border-radius: 1rem;
  text-transform: uppercase;
  font-weight: 900;
  border: 1px solid transparent;
  transition: 300ms all;
}
.boton-rojo:hover {
  cursor: pointer;
  background-color: transparent;
  color: #181818;
  border: 1px solid #ad0f0f;
}

.boton-azul {
  display: block;
  text-align: center;
  width: 100%;
  color: #dfdddd;
  background-color: #146ab1;
  padding: 2rem;
  border-radius: 1rem;
  text-transform: uppercase;
  font-weight: 900;
  border: 1px solid transparent;
  transition: 300ms all;
}
.boton-azul:hover {
  background-color: transparent;
  color: #181818;
  border: 1px solid #146ab1;
}

.hover-blanco:hover {
  color: #dfdddd;
}

@media (min-width: 768px) {
  .boton-verde {
    width: 25rem;
    margin: 2rem 0;
  }
}
.nosotros {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  margin-bottom: 10rem;
}
.nosotros .texto-nos .titulo {
  margin: 3rem 0;
  font-size: 3rem;
}
.nosotros .texto-nos .texto {
  margin-top: 0;
  font-size: 2.5rem;
}

@media (min-width: 768px) {
  .nosotros {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 3rem;
  }
  .nosotros .texto-nos .titulo {
    margin: 0;
  }
}
.formulario {
  padding: 3rem;
}
.formulario legend {
  font-size: 3rem;
}
.formulario p {
  font-weight: 900;
}
.formulario p, .formulario label, .formulario input, .formulario textarea, .formulario select, .formulario option {
  font-size: 2.5rem;
}
.formulario input, .formulario textarea, .formulario select {
  padding: 1.5rem;
  border-radius: 1rem;
  border: 1px solid #a7a5a5;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 25rem;
}
.formulario fieldset {
  padding: 2rem;
}

.contenido-contacto {
  display: flex;
  flex-direction: column;
}

.forma-contacto {
  display: flex;
  flex-direction: row;
}
.forma-contacto .forma {
  margin-right: 5rem;
}
.forma-contacto .forma label {
  margin-right: 1rem;
  text-transform: uppercase;
}

.submit {
  border: none;
  margin-top: 5rem;
}
.submit:hover {
  cursor: pointer;
}

.texto-contacto {
  margin-top: 0;
}

.fieldset {
  margin-bottom: 5rem;
  border: 1px solid #a7a5a5;
}
.fieldset .fecha input:hover {
  cursor: pointer;
}

.imagen-anuncio {
  margin-bottom: 5rem;
}
.imagen-anuncio img {
  width: 100%;
}

.caracteristica-anuncio {
  justify-content: flex-start !important;
}
.caracteristica-anuncio .caracteristica {
  margin-right: 5rem;
}

span {
  color: #E08709;
}

.alerta {
  padding: 1rem;
  color: #dfdddd;
  font-size: 2.2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem auto;
}
.alerta.error {
  background-color: #ad0f0f;
}
.alerta.exito {
  background-color: #1a850b;
}

.tabla-propiedades {
  margin-top: 5rem;
  font-size: 2.5rem;
  width: 100%;
  border-spacing: 0;
}
.tabla-propiedades thead {
  background-color: #1a850b;
  font-size: 3rem;
}
.tabla-propiedades thead th {
  padding: 2rem;
  color: #dfdddd;
}
.tabla-propiedades tbody {
  text-align: center;
}
.tabla-propiedades tbody .imagen-tabla {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tabla-propiedades tbody img {
  width: 30rem;
  margin-top: 2rem;
}

.imagen-small {
  width: 80rem;
  margin-bottom: 6rem;
}

.w-100 {
  margin-bottom: 2rem;
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.contenedor-header {
  background-position: center center;
  background-size: cover;
  height: 90rem;
}

.base {
  background-color: #383838;
  padding-bottom: 5rem;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}
.contenido-header .header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 5rem;
}
.contenido-header .header .logo {
  width: 50rem;
}
.contenido-header h1 {
  color: #dfdddd;
  padding-bottom: 5rem;
  margin-left: 3rem;
  text-align: left;
}

.contenedor-navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contenedor-navegacion .utilidades-navegacion {
  display: flex;
  flex-direction: row;
  margin-left: 5rem;
}
.contenedor-navegacion .utilidad {
  width: 4rem;
  margin-right: 3rem;
  filter: invert(80%);
}

.menu-mobile {
  filter: invert(25%) !important;
}

.navegacion-principal {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}

.mostrar {
  visibility: visible;
  opacity: 1;
}

.navegacion {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.navegacion a {
  color: #dfdddd;
  margin-left: 2rem;
  padding: 1rem;
}
.navegacion a:hover {
  color: #E08709;
}

@media (min-width: 768px) {
  .contenido-header .header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 5rem;
  }
  .contenido-header .header .logo {
    width: 50rem;
  }

  .contenedor-navegacion {
    display: flex;
    flex-direction: row;
  }
  .contenedor-navegacion .navegacion-principal {
    visibility: visible;
    opacity: 1;
  }
  .contenedor-navegacion .navegacion-principal a:first-of-type {
    margin-left: 0;
  }
  .contenedor-navegacion .menu-mobile {
    display: none;
  }

  .navegacion {
    flex-direction: row;
  }
}
.dark-mode:hover {
  cursor: pointer;
}

.body-darkMode {
  background-color: #181818;
  color: #dfdddd;
}
.body-darkMode .propiedad {
  background-color: #383838 !important;
  border: 1px solid #000000 !important;
}
.body-darkMode .caracteristica img {
  filter: invert(80%);
}
.body-darkMode .boton-amarillo:hover {
  color: #dfdddd;
}
.body-darkMode .boton-verde:hover {
  color: #dfdddd;
}
.body-darkMode .boton-rojo:hover {
  color: #dfdddd;
}
.body-darkMode .boton-azul:hover {
  color: #dfdddd;
}
.body-darkMode .blog a {
  color: #dfdddd !important;
}
.body-darkMode .formulario .fieldset {
  border: 1px solid #383838;
}
.body-darkMode .formulario .fieldset input, .body-darkMode .formulario .fieldset textarea, .body-darkMode .formulario .fieldset select {
  background-color: #383838;
  border: #a7a5a5;
  color: #dfdddd;
}
.body-darkMode .formulario .boton-verde {
  border: 1px solid #1a850b;
}

.contenido-nosotros {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.servicios {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  border-bottom: 1px solid #a7a5a5;
  margin-left: 0;
}
.servicios:last-of-type {
  border-bottom: none;
}

.icono {
  height: 15rem;
  width: auto;
}

.titulo-servicio {
  text-transform: uppercase;
  margin-bottom: 0;
}

.texto-servicio {
  text-align: center;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    flex-direction: row;
  }

  .servicios {
    border-bottom: none;
  }
}
.propiedades {
  display: flex;
  flex-direction: column;
  padding: 2rem;
}
.propiedades .propiedad {
  background-color: #cfcccc;
  border: 1px solid #bbbaba;
  margin-bottom: 5rem;
}
.propiedades .propiedad:last-of-type {
  margin-bottom: 0;
}
.propiedades .datos-propiedad {
  padding: 2rem;
}
.propiedades .datos-propiedad a {
  display: block;
  text-align: center;
}

.caracteristicas {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.caracteristicas .caracteristica {
  display: flex;
  gap: 2rem;
}
.caracteristicas .caracteristica p {
  font-weight: 900;
}
.caracteristicas .caracteristica img {
  height: 7rem;
  margin: 3rem 0;
}

.texto-propiedad {
  font-size: 2.5rem;
  margin-bottom: 0;
}

.precio-propiedad {
  color: #1a850b;
  font-weight: 900;
  font-size: 4rem;
  margin: 0;
}

.boton-ver-todos {
  margin: 5rem 2rem;
}

@media (min-width: 768px) {
  .propiedades {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 3rem;
    row-gap: 5rem;
    padding: 0;
  }
  .propiedades .propiedad {
    margin-bottom: 0;
  }

  .caracteristicas .caracteristica {
    gap: 1rem;
  }

  .boton-ver-todos {
    margin: 5rem 0;
    display: flex;
    justify-content: flex-end;
  }
}
.webp .hero {
  background-image: url(../img/encuentra.webp);
}

.no-webp .hero {
  background-image: url(../img/encuentra.jpg);
}

.hero {
  background-position: center center;
  background-size: cover;
  padding: 25rem 0;
  color: #dfdddd;
  text-align: center;
}

.titulo-hero {
  font-weight: 700;
  font-size: 5rem;
}

.texto-hero {
  font-size: 2.5rem;
  margin: 0 2rem 5rem 2rem;
}

.blog-testimonial {
  display: flex;
  flex-direction: column;
}
.blog-testimonial h2 {
  margin-bottom: 0;
}

.blog {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  gap: 3rem;
  margin-bottom: 3rem;
  border-bottom: 1px solid #a7a5a5;
}
.blog:last-of-type {
  border-bottom: none;
  margin-bottom: 0;
}
.blog .imagen-blog {
  width: 50rem;
}
.blog .imagen-blog2 {
  width: 54rem;
}
.blog .texto-blog .titulo-blog {
  text-align: left;
  font-weight: 700;
  font-size: 3.5rem;
  margin-top: 0;
}
.blog .texto-blog .titulo-blog::after {
  content: "";
  display: block;
  width: 20rem;
  height: 0.8rem;
  background-color: #1a850b;
  margin-top: 1rem;
}
.blog .texto-blog span {
  color: #E08709;
}
.blog .texto-blog p {
  font-size: 2.5rem;
}

.testimonial {
  text-align: center;
}

.contenido-testimonial {
  display: inline-block;
  background-color: #1a850b;
  padding: 1rem;
  font-size: 2.5rem;
  color: #dfdddd;
  border-radius: 2rem;
  width: 90%;
  margin-top: 3rem;
}
.contenido-testimonial blockquote {
  padding-left: 5rem;
  position: relative;
  font-size: 3rem;
}
.contenido-testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.contenido-testimonial p {
  text-align: right;
  color: #dfdddd;
  font-size: 2.5rem;
  margin: 4rem 2rem;
}

@media (min-width: 768px) {
  .blog {
    flex-direction: row;
  }
  .blog .imagen-blog {
    margin-bottom: 4rem;
    width: 50rem;
  }
  .blog .imagen-blog2 {
    width: 54rem;
  }
}
@media (min-width: 1024px) {
  .blog-testimonial {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 5rem;
  }

  .blog {
    margin-bottom: 0;
  }
  .blog .imagen-blog {
    margin-bottom: 0;
    width: 62rem;
  }
  .blog .imagen-blog2 {
    width: 66rem;
  }

  .titulo-blog {
    margin-top: 3rem;
  }
}
.footer {
  text-align: center;
  padding: 3rem 0;
  background-color: #383838;
  margin-top: 5rem;
}

.contenido-footer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.navegacion-footer {
  flex-direction: row;
}
.navegacion-footer a {
  text-align: left;
}

.copyright {
  color: #dfdddd;
  font-size: 2.5rem;
}

